.dashboard-grid {
    display: grid;
    grid-template-columns: 24rem 1fr;
    grid-template-rows: 5rem 1fr 5rem;
    grid-template-areas: 
    'sidenav header'
    'sidenav main'
    'sidenav footer';
    height: 100vh; 

    .header {
        grid-area: header;
        background-color: $color-green;
    }

    .aside {
        grid-area: sidenav;
        background-color: $color-primary-black;
    }

    .main {
        color: $color-primary-black;
        grid-area: main;
        background-color: $color-white;
    }

    .footer {
        grid-area: footer;
        background-color: $color-primary-black
    }
}

