* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Montserrat', sans-serif;
  position: relative;
  z-index: -999;
  background: black;
  background: -moz-linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  background: -webkit-linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  background: linear-gradient(270deg, black 0%, #3f3f3f 71%, #505050 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#505050", GradientType=1);
  color: #fff; }

#root {
  position: relative;
  z-index: -999; }

a {
  text-decoration: none;
  color: white; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: white;
  box-shadow: 0 0 0px 1000px none inset;
  transition: background-color 5000s ease-in-out 0s; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@media all and (max-width: 680px) {
  html {
    font-size: 35%; } }

.dashboard-grid {
  display: grid;
  grid-template-columns: 24rem 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-areas: 'sidenav header' 'sidenav main' 'sidenav footer';
  height: 100vh; }
  .dashboard-grid .header {
    grid-area: header;
    background-color: #00adb5; }
  .dashboard-grid .aside {
    grid-area: sidenav;
    background-color: #303841; }
  .dashboard-grid .main {
    color: #303841;
    grid-area: main;
    background-color: #eeeeee; }
  .dashboard-grid .footer {
    grid-area: footer;
    background-color: #303841; }
